// app/products/[slug]/landing-page/page.tsx

import LandingPageBuilder from "@/app/landing-pages/new/page";

interface PageProps {
  params: {
    slug: string;
  };
}

export default function ProductLandingPageBuilder({ params }: PageProps) {
  console.log("Raw slug from URL:", params.slug);

  const {slug} = params;

  // Convert slug to number
  const productId = Number(slug);

  // If numeric slug
  if (!isNaN(productId) && productId > 0) {
    return <LandingPageBuilder productId={productId} />;
  }

  // Non-numeric slug fallback
  return (
    <div className="flex h-screen items-center justify-center flex-col">
      <p className="text-xl text-red-600">
        Invalid or unsupported product slug: "{slug}"
      </p>
      <p className="text-muted-foreground mt-4">
        Currently only numeric product IDs are supported.
      </p>
    </div>
  );
}
